:root {
  --view-height: 100vh;
}

html,
body {
  height: 100vh;
  height: var(--view-height);
  font-size: small;
}

@media screen and (max-width: 320px) {
  body,
  html {
    font-size: x-small;
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root {
  height: 100vh;
  height: var(--view-height);
}

.hidden {
    display: none;
    visibility: hidden;
}

/* CODE FROM: https://codesandbox.io/s/n3rrym5y1l */

/* TRANSITION WRAPPER */
.transition-wrapper {
  /* height: 100%; */
  position: relative;
  z-index: 1;
}

.transition-wrapper .transition-item {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
}

/* HOME PAGE */
.home-page {
  height: var(--view-height);
  /* height: 100vh; */
  /* height: 100%; */
  transition: opacity 0.5s, -webkit-transform 0.5s;
  transition: transform 0.5s, opacity 0.5s;
  transition: transform 0.5s, opacity 0.5s, -webkit-transform 0.5s;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
  z-index: 1;
}

.home-page.transition-appear {
  opacity: 1;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

.home-page.transition-appear.transition-appear-active {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.home-page.transition-leave {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}

.home-page.transition-leave.transition-leave-active {
  opacity: 1;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}

/* GLOBAL MENU PAGE */
.menu-page {
  height: var(--view-height);
  /* height: 100vh; */
  /* height: 100%; */
}

.menu-page.transition-appear {
  opacity: 1;
}

.menu-page.transition-appear.transition-appear-active {
  opacity: 1;
}

.menu-page.transition-leave {
  opacity: 0;
}

.menu-page.transition-leave.transition-leave-active {
  opacity: 0;
}

